<template>
  <div class="container">
    <div class="row">
      <div class="col-2">
        <!--  -->
        <Navbar />
      </div>

      <div class="col-7">
        <!--  Spinner -->
        <Spinner v-if="isLoading" />

        <!--  -->
        <router-view
          v-else
          :theTweetId="theTweetId"
          :initialTweets="tweets"
          :popular="topPopular"
          @after-click-reply="afterClickReply"
        />
      </div>

      <div class="col-3">
        <!--  -->
        <Followboard />
      </div>

      <!--  -->
      <TweetModal @after-submit-tweet="afterSubmitTweet" />
      <ReplyModal
        :replyModalData="replyModalData"
        @main-after-submit-reply="mainAfterSubmitReply"
      />
    </div>
  </div>
</template>

<script>
import Navbar from "../components/Navbar.vue";
import Spinner from "../components/Spinner.vue";
import Followboard from "../components/Followboard";
import TweetModal from "../components/TweetModal";
import ReplyModal from "../components/ReplyModal.vue";
import tweetsAPI from "../apis/tweets";
import { mapState } from "vuex";
import { Toast } from "../utils/helpers";

export default {
  name: "Main",
  computed: {
    ...mapState(["currentUser", "topPopular"]),
  },
  components: {
    Followboard,
    Navbar,
    ReplyModal,
    TweetModal,
    Spinner,
  },
  data() {
    return {
      tweets: [],
      isLoading: true,
      //  ReplyModal 
      replyModalData: {},
      //  ID -1
      //  MainPage 
      theTweetId: -1,
    };
  },
  created() {
    // 
    this.fetchTweets();
  },
  methods: {
    //  API 
    async fetchTweets() {
      try {
        //  spinner 
        this.isLoading = true;

        //  API 
        const response = await tweetsAPI.tweets.getTweets();
        if (response.statusText !== "OK") {
          throw new Error("");
        }

        //  tweets
        this.tweets = response.data;

        //  spinner 
        this.isLoading = false;
      } catch (error) {
        //  spinner 
        this.isLoading = false;

        // 
        console.error(error.message);
        Toast.fire({
          icon: "error",
          title: "",
        });
      }
    },

    //  MainPage (Main) ReplyModal 
    mainAfterSubmitReply(id) {
      // + 1
      this.theTweetId = id;
    },

    // 
    afterSubmitTweet(payload) {
      const {
        id, // tweetID
        description,
        UserId,
        account,
        commentCount,
        createdAt,
        likeCount,
        name,
        avatar,
      } = payload;


      this.tweets.unshift({
        id, // tweetID
        UserId,
        description,
        account,
        name,
        createdAt,
        likeCount,
        commentCount,
        avatar,
        isLike: false,
      });
    },

    //  replyModal
    afterClickReply(payload) {
      const { id, avatar, name, account, createdAt, description } = payload;

      this.replyModalData = {
        id, // tweetID
        avatar,
        name,
        account,
        createdAt,
        description,
      };
    },
  },
};
</script>



