<template>
  <div class="mx-auto" style="width: 400px">
    <div class="mx-auto" style="width: 40px">
      <img
        class="mx-auto mt-5"
        width="40px"
        src="https://i.postimg.cc/Dfp23k8g/logo-2x.png"
        alt="LogoImage"
      />
    </div>
    <h3 class="title text-center font-weight-bold mt-4"> Alphitter</h3>

    <form @submit.prevent.stop="handleSubmit">
      <div class="form-wrapper">
        <label for="account"></label>
        <div>
          <input
            id="account"
            v-model="account"
            name="account"
            type="text"
            class="form"
            :class="{ redBottomLine: errorMsg === '' }"
            placeholder=""
            required
            autofocus
          />
          <div class="error-alert">
            <span v-if="errorMsg === '!'"></span>
          </div>
        </div>
      </div>

      <div class="form-wrapper">
        <label for="password"></label>
        <div>
          <input
            id="password"
            v-model="password"
            name="password"
            type="password"
            class="form"
            :class="{ redBottomLine: errorMsg === '' }"
            placeholder=""
            autocomplete="current-password"
            required
          />
          <div class="error-alert">
            <span v-if="errorMsg === '!'"></span>
          </div>
        </div>
      </div>

      <div class="mt-4">
        <button
          type="submit"
          class="btn btn-info btn-singin"
          :disabled="isProcessing"
        >
          {{ isProcessing ? "" : "" }}
        </button>
      </div>
    </form>
    <div class="d-flex mt-4 justify-content-end">
      <router-link class="mx-1" to="/signup">  </router-link>
      <span></span>
      <router-link class="mx-1" to="/admin">  </router-link>
    </div>
  </div>
</template>

<script>
import { Toast } from "./../utils/helpers";
import authorizationAPI from "./../apis/authorization";

export default {
  name: "Login",
  data() {
    return {
      account: "",
      password: "",
      isProcessing: false,
      errorMsg: "",
    };
  },
  methods: {
    async handleSubmit() {
      try {
        // 
        this.errorMsg = "";

        //  required 
        if (!this.account || !this.password) {
          // STEP1.  email  password  Toast 
          Toast.fire({
            icon: "warning",
            title: "",
          });
          // STEP2.  return 
          return;
        }

        // 
        // STEP1. 
        this.isProcessing = true;

        // STEP2.  API 
        const { data } = await authorizationAPI.signIn({
          account: this.account,
          password: this.password,
        });

        // STEP3-1. API 
        if (data.status === "error") {
          throw new Error(data.message);
        }

        // STEP3-2. 
        //  token  localStorage 
        localStorage.setItem("token", data.data.token);

        //  API  Vuex 
        this.$store.commit("setCurrentUser", data.data.user);

        // ,   isProcessing 
        this.$router.push("/main");

        // 
      } catch (error) {
        // 
        this.password = "";

        // 
        this.isProcessing = false;

        // 
        console.error(error.message);
        this.errorMsg = error.message;
        if (error.message === "") {
          Toast.fire({
            icon: "error",
            title: "",
          });
        } else if (error.message === "") {
          Toast.fire({
            icon: "error",
            title: "",
          });
        } else {
          Toast.fire({
            icon: "error",
            title: "",
          });
        }
      }
    },
  },
};
</script>


<style lang="scss">
@import "../assets/scss/login.scss";
</style>