import Vue from 'vue'
import Vuex from 'vuex'
import userAPI from "./../apis/user"
import { Toast } from './../utils/helpers'


Vue.use(Vuex)


export default new Vuex.Store({
  // 
  state: {
    currentUser: {
      id: - 1,
      name: '',
      account: '',
      email: '',
      avatar: '',
      role: ''
    },
    isAuthenticated: false,
    token: '',
    topPopular: []
  },
  getters: {
  },
  //  commit  state 
  mutations: {
    // 
    setCurrentUser(state, currentUser) {
      // 1.  API  currentUser  Vuex state  currentUser
      state.currentUser = {
        ...state.currentUser,
        ...currentUser
      }
      // 2.  true
      state.isAuthenticated = true
      // 3.  token  localStorage  state 
      state.token = localStorage.getItem('token')
    },

    // 
    revokeAuthentication(state) {
      // 1.  currentUser 
      state.currentUser = {}
      // 2.  false
      state.isAuthenticated = false
      // 3.  state  token
      state.token = ""
      // 4.  localStorage  token 
      localStorage.removeItem('token')
    },

    // 
    setTopPopular(state, topPopular) {
      state.topPopular = [...topPopular]
    }
  },
  //  dispatch  API 
  actions: {
    //  API 
    async fetchCurrentUser({ commit }) {
      try {
        const { data } = await userAPI.getCurrentUser()
        if (data.status === 'error') {
          throw new Error(data.message)
        }
        // 
        //  currentUser 
        const { id, name, account, email, avatar, role } = data
        commit('setCurrentUser', {
          id, name, account, email, avatar, role
        })
        return { isAuthenticated: true, role: this.state.currentUser.role }
        // 
      } catch (error) {
        console.error(error.message)
        commit('revokeAuthentication');
        return { isAuthenticated: false, role: '' };
      }
    },

    //  API 
    async fetchPopular({ commit }) {
      try {
        const response = await userAPI.getTopUsers();
        const { data } = response;
        if (response.statusText !== "OK") {
          throw new Error(data.message);
        }
        commit('setTopPopular', data)
      } catch (error) {
        console.error(error.message);
        Toast.fire({
          icon: "error",
          title: "",
        });
      }
    },
  },
  modules: {
  }
})
