<template>
  <div class="container">
    <div class="row">
      <!-- Navbar -->
      <div class="col-2">
        <Navbar />
      </div>
      <!-- setting -->
      <div class="col">
        <div class="user-section">
          <div class="title">
            <h4 class="ml-4 pt-4"></h4>
          </div>
          <div class="content">
            <form class="pl-4 pr-4" @submit.prevent.stop="handleSubmit">
              <div class="form-wrapper mt-4">
                <label for="account"></label>
                <div>
                  <input
                    id="account"
                    v-model="account"
                    name="account"
                    type="text"
                    class="form"
                    :class="{ redBottomLine: errorMsg === '' }"
                    placeholder=""
                    required
                    autofocus
                  />
                  <div class="error-alert">
                    <span v-if="errorMsg === ''"
                      ></span
                    >
                  </div>
                  <div class="alert-container">
                    <span class="error-alert" v-if="account.length > 15"
                      ></span
                    >
                    <span class="letter-count" v-if="account.length > 0"
                      >{{ account.length }}/15</span
                    >
                  </div>
                </div>
              </div>

              <div class="form-wrapper mt-4">
                <label for="account"></label>
                <div>
                  <input
                    id="name"
                    v-model="name"
                    name="name"
                    type="text"
                    class="form"
                    :class="{ redBottomLine: name.length > 50 }"
                    placeholder=""
                    required
                    autofocus
                  />
                  <div class="alert-container">
                    <span class="error-alert" v-if="name.length > 50"
                      ></span
                    >
                    <span class="letter-count" v-if="name.length > 0"
                      >{{ name.length }}/50</span
                    >
                  </div>
                </div>
              </div>

              <div class="form-wrapper mt-4">
                <label for="account">Email</label>
                <div>
                  <input
                    id="email"
                    v-model="email"
                    name="email"
                    type="email"
                    class="form"
                    :class="{
                      redBottomLine:
                        errorMsg === '' ||
                        errorMsg === '',
                    }"
                    placeholder=" Email"
                    required
                    autofocus
                  />
                  <div class="error-alert">
                    <span v-if="errorMsg === ''"
                      ></span
                    >
                  </div>
                  <div class="error-alert">
                    <span v-if="errorMsg === ''"
                      ></span
                    >
                  </div>
                </div>
              </div>

              <div class="form-wrapper mt-4">
                <label for="password"></label>
                <div>
                  <input
                    id="password"
                    v-model="password"
                    name="password"
                    type="password"
                    class="form"
                    placeholder=""
                    required
                  />
                </div>
              </div>

              <div class="form-wrapper mt-4">
                <label for="checkPassword"></label>
                <div>
                  <input
                    id="checkPassword"
                    v-model="checkPassword"
                    name="checkPassword"
                    type="password"
                    class="form"
                    :class="{
                      redBottomLine: errorMsg === '',
                    }"
                    placeholder=""
                    required
                  />
                  <div class="error-alert">
                    <span v-if="errorMsg === ''"
                      ></span
                    >
                  </div>
                </div>
              </div>

              <div class="mt-4 d-flex justify-content-end">
                <button
                  type="submit"
                  class="btn btn-info"
                  :disabled="isProcessing"
                >
                  {{ isProcessing ? "" : "" }}
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import userAPI from "./../apis/user";
import Navbar from "../components/Navbar.vue";
import { Toast } from "./../utils/helpers";
import { mapState } from "vuex";

export default {
  name: "Setting",
  components: {
    Navbar,
  },
  data() {
    return {
      // 
      id: -1,
      account: "",
      name: "",
      email: "",
      password: "",
      checkPassword: "",
      isProcessing: false,
      errorMsg: "",
    };
  },
  computed: {
    //  Vuex state  currentUser 
    ...mapState(["currentUser"]),
  },
  watch: {
    //  watch  currentUser 
    currentUser(newValue) {
      this.currentUser = {
        ...this.currentUser,
        ...newValue,
      };
      // 
      (this.account = this.currentUser.account),
        (this.name = this.currentUser.name),
        (this.email = this.currentUser.email),
        (this.id = this.currentUser.id);
    },
  },
  created() {
    //  created   currentUser 
    this.fetchAccountData();
  },
  methods: {
    //  currentUser 
    fetchAccountData() {
      (this.account = this.currentUser.account),
        (this.name = this.currentUser.name),
        (this.email = this.currentUser.email),
        (this.id = this.currentUser.id);
    },
    // 
    async handleSubmit() {
      try {
        // 
        this.errorMsg = "";

        // 
        // STEP1.  required 
        if (
          !this.account.trim() ||
          !this.email.trim() ||
          !this.name.trim() ||
          !this.password.trim() ||
          !this.checkPassword.trim()
        ) {
          Toast.fire({
            icon: "warning",
            title: "",
          });
          return;

          // STEP2. 
        } else if (this.account.length > 15) {
          Toast.fire({
            icon: "warning",
            title: " 15 ",
          });
          return;

          // STEP3. 
        } else if (this.name.length > 50) {
          Toast.fire({
            icon: "warning",
            title: " 50 ",
          });
          return;

          // STEP4. 
        } else if (this.password.length !== this.checkPassword.length) {
          this.checkPassword = "";
          this.errorMsg = "";
          Toast.fire({
            icon: "warning",
            title: "",
          });
          return;
        }

        // 
        // STEP1. 
        this.isProcessing = true;

        // STEP2.  API 
        const newData = {
          account: this.account,
          name: this.name,
          email: this.email,
          password: this.password,
          checkPassword: this.checkPassword,
        };

        const { data } = await userAPI.updateAccount({
          userId: this.id,
          newData,
        });

        // API 
        if (data.status === "error") {
          throw new Error(data.message);
        }

        // 
        Toast.fire({
          icon: "success",
          title: "",
        });

        // 
        this.password = "";
        this.checkPassword = "";

        // 
        this.isProcessing = false;

        // 
      } catch (error) {
        // 
        this.isProcessing = false;

        // 
        console.error(error.message);
        if (error.message === "") {
          this.errorMsg = error.message;
          Toast.fire({
            icon: "error",
            title: "",
          });
        } else if (error.message === "") {
          this.errorMsg = error.message;
          Toast.fire({
            icon: "error",
            title: " Email",
          });
        } else if (error.message === "") {
          Toast.fire({
            icon: "error",
            title: "",
          });
        } else if (error.message === "") {
          this.checkPassword = "";
          Toast.fire({
            icon: "error",
            title: "",
          });
        } else {
          this.errorMsg = error.message;
          Toast.fire({
            icon: "error",
            title: "",
          });
        }
      }
    },
  },
};
</script>


