import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import Main from '../views/Main.vue'
import User from '../views/User.vue'
// import Profile from '../views/Profile.vue'
import Setting from '../views/Setting.vue'
import store from './../store'
import { Toast } from "./../utils/helpers";

Vue.use(VueRouter)


const routes = [
  // 
  {
    path: '/',
    name: 'root',
    redirect: '/login'
  },

  // 
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/signup',
    name: 'sign-up',
    component: () => import('../views/SignUp.vue')
  },
  {
    path: '/main/',
    name: 'main',
    redirect: '/main/mainpage',
    component: Main,
    children: [
      {
        name: 'main-page',
        path: 'mainpage',
        component: () => import('../views/MainPage'),
      },
      {
        name: 'reply-list',
        path: 'replylist/:id',
        component: () => import('../views/ReplyList'),
      },
    ]
  },
  // {
  //   path: '/profile',
  //   name: 'profile',
  //   component: Profile,
  //   children: [
  //     {
  //       name: 'user-tweet',
  //       path: '/tweet',
  //       component: () => import('../components/UserTweet'),
  //     },
  //   ]
  // },
  {
    path: '/setting',
    name: 'setting',
    component: Setting
  },


  {
    path: '/users/:id',
    name: 'user',
    // redirect: '/users/:id/tweets',
    component: User,
    // children: [
    //   {
    //     name: 'main-tweets',
    //     path: 'tweets',
    //     component: () => import('../views/MainTweets'),
    //   },
    //   {
    //     name: 'replies',
    //     path: 'replies',
    //     component: () => import('../views/Replies'),
    //   },
    //   {
    //     name: 'liked-tweets',
    //     path: 'liked',
    //     component: () => import('../views/likedTweets'),
    //   },
    // ]
  },

  // 
  {
    path: '/admin',
    name: 'admin',
    component: () => import('../views/Admin.vue')
  },
  {
    path: '/admin/tweets',
    name: 'admin-tweets',
    component: () => import('../views/AdminTweets.vue'),
  },
  {
    path: '/admin/users',
    name: 'admin-users',
    component: () => import('../views/AdminUsers.vue'),
  },

  // not-found 
  {
    path: '*',
    name: 'not-found',
    component: () => import('../views/NotFound.vue')
  }
]

const router = new VueRouter({
  routes
})



//  currentUser 
router.beforeEach(async (to, from, next) => {
  //  localStorage  token
  const token = localStorage.getItem('token')

  //  token 
  const pathWithoutToken = ['login', 'sign-up', 'admin']

  // 
  const pathAllForAdmin = ['login', 'sign-up', 'admin', 'admin-tweets', 'admin-users']

  // 
  const pathOnlyForAdmin = ['admin-tweets', 'admin-users']


  //  token, 
  if (!token && !pathWithoutToken.includes(to.name)) {
    Toast.fire({
      icon: 'warning',
      title: ''
    })
    next('login')
    return


    //  token
  } else if (token) {
    // 
    let { isAuthenticated, role } = await store.dispatch('fetchCurrentUser')

    // 
    if (!isAuthenticated && !pathWithoutToken.includes(to.name)) {
      Toast.fire({
        icon: 'warning',
        title: ''
      })
      next('/login')
      return
    }


    // 
    if (isAuthenticated && role === 'user') {
      if (to.name === 'sign-up' || to.name === 'login') {
        next('/main')
        return

        // 
      } else if (pathOnlyForAdmin.includes(to.name)) {
        Toast.fire({
          icon: 'warning',
          title: ''
        })
        next(from)
        return
      }



      // 
    } else if (isAuthenticated && role === 'admin') {
      if (to.name === 'admin') {
        next('/admin/tweets')
        return

        // , 
      } else if (!pathAllForAdmin.includes(to.name)) {
        Toast.fire({
          icon: 'warning',
          title: ''
        })
        next(from)
        return
      }
    }
    next()
  }

  next()
})


export default router
